import React from 'react'
import { createPortal } from 'react-dom'
import styled from 'styled-components'
import Logo from './Logo'

const Wrapper = styled.div`
  color: #ffa023;
  padding: 16px;
  background-color: #f1f7fc;
  margin: 0 auto;
  width: 400px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
`

const Title = styled.h1`
  color: #ffa023;
  font-size: 24px;
`

const Desc = styled.h2`
  color: #273caf;
  font-size: 16px;
`

export const MyPortalComponent = () => (
  <Wrapper className='App'>
    <Logo />
    <Title>CROP COMPONENT</Title>
    <Desc>
      I am an awesome component to be rendered in React Portal and waiting to be
      crop.
    </Desc>
  </Wrapper>
)

export default function MyComponent() {
  const myPortal = document.getElementById('portal')

  return createPortal(<MyPortalComponent />, myPortal)
}
