import React, { useState } from 'react'
import html2canvas from 'html2canvas'
import MyComponent, { MyPortalComponent } from './components/MyComponent'
import styled from 'styled-components'

const Modal = styled.div`
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
`

function App() {
  const [screenshotTaken, setScreenshotTaken] = useState(false)
  const myPortalContainer = document.getElementById('portal')
  const myCanvas = document.querySelector('div#portal canvas')

  // useEffect(() => {
  //   if (myCanvas) {
  //
  //   }
  // }, [screenshotTaken, myCanvas])
  return (
    <div className='App'>
      <header className='App-header'>
        <button
          onClick={() =>
            html2canvas(myPortalContainer, { width: 400, height: 200 }).then(
              function (canvas) {
                myPortalContainer.innerHTML = ''
                myPortalContainer.appendChild(canvas)
                setScreenshotTaken(true)
              }
            )
          }
        >
          TAKE A SNAPSHOT
        </button>
        <MyComponent />

        {screenshotTaken && (
          <Modal>
            <MyPortalComponent />
            <a href={myCanvas && myCanvas.toDataURL()} download='CLEO-one.png'>
              DOWNLOAD
            </a>
          </Modal>
        )}
      </header>
    </div>
  )
}

export default App
